import React from 'react';
import './CircleSelector.css'

const CircleSelector = (props) => {
  return (
    <div className="CircleSelector">
      <button
        className={props.buttonSelected === 1 ? 'selected' : ''}
        onClick={() => props.handleButton(1)}
      >
        {props.buttonSelected === 1 ? 'CIRCLE 1 SELECTED' : 'SELECT CIRCLE 1'}
      </button>
      <button
        className={props.buttonSelected === 2 ? 'selected' : ''}
        onClick={() => props.handleButton(2)}
      >
        {props.buttonSelected === 2 ? 'CIRCLE 2 SELECTED' : 'SELECT CIRCLE 2'}
      </button>
      <button
        className={props.buttonSelected === 3 ? 'selected' : ''}
        onClick={() => props.handleButton(3)}
      >
        {props.buttonSelected === 3 ? 'CIRCLE 3 SELECTED' : 'SELECT CIRCLE 3'}
      </button>
      <button
        className={props.buttonSelected === 4 ? 'selected' : ''}
        onClick={() => props.handleButton(4)}
      >
        {props.buttonSelected === 4 ? 'CIRCLE 4 SELECTED' : 'SELECT CIRCLE 4'}
      </button>
      
    </div>
  );
}



export default CircleSelector;
